          case "transform_columns_replace":
            //没有实现阴影效果
            res = generateDataForReplace(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              replace_value
            );
            transform_columns_replace_na(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              input_explict_col,
              res.naRow,
              i,
              this.show_table_name
            );
            break;
          case "transform_columns_mutate":
            res = generateDataForMutate_cover(
              dataIn1_csv,
