            );
            transform_columns_mutate(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              input_explict_col,
              output_explict_col,
              i,
              this.show_table_name
            );
            break;
          case "transform_columns_rename":
            res = generateDataForColumnRename(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col
            );
            transform_columns_mutate(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.expAfter,
              res.expAfter,
              i,
              this.show_table_name
            );
            break;
          case "combine_columns_merge":
            res = generateDataForMergeColumns(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              output_explict_col
            );
            combine_columns_merge(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.newInExpOrImp,
              res.newOutExpOrImp,
              res.outColors,
              i,
              this.show_table_name
            );
            break;
          case "combine_columns_mutate":
            res = generateDataForMergeColumns(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              output_explict_col
            );
            combine_columns_merge(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.newInExpOrImp,
              res.newOutExpOrImp,
              res.outColors,
              i,
              this.show_table_name
            );
            break;
          // case 'combine_rows_sum':
          //   res = generateDataForRowsSum(dataIn1_csv,dataOut1_csv)
          //   combine_rows_sum(res.m1,res.m2,rule,input_table_name,output_table_name,i,this.show_table_name)
          //   break
          case "combine_rows_summarize":
            //这个操作再看看
            if (input_explict_col.length === 0) {
              input_explict_col = Array.from(
                new Array(dataIn1_csv[0].length),
                (x, i) => i
              );
            }
            res = generateDataForGroupSummarize(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              output_explict_col,
              input_implict_col
            );
            combine_rows_sum(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              i,
              this.show_table_name
            );
            break;
          case "combine_rows_interpolate":
            res = generateDataForRowInterpolate(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col
            );
            combine_rows_interpolate(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.naPos,
              i,
              this.show_table_name
            );
            break;
          case "transform_rows_edit":
            res = generateDataForEditRow(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_row
            );
            transform_rows_edit(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.rowIndex,
              i,
              this.show_table_name
            );
            break;
          case "separate_tables_subset":
            res = generateDataForSeparateSubset(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col
            );
            separate_tables_subset(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              output_table_name,
              output_table_name,
              res.outColor1,
              res.outColor2,
              i,
              this.show_table_name
            );
            break;
          case "separate_tables_decompose":
            res = generateDataForSeparateDecompose(
              dataIn1_csv,
              input_explict_col
            );
            separate_tables_decompose(
              res.m1,
              res.tables,
              rule,
              input_table_name,
              i,
              this.show_table_name
            );
            break;
          case "separate_tables_decompose_q":
            res = generateDataForSeparateDecompose_q(
              dataIn1_csv,
