              this.show_table_name
            );
            break;
          case "separate_tables_split":
            res = generateDataForSeparateSplit(
              dataIn1_csv,
              input_explict_col,
              input_implict_col
            );
            separate_tables_split(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              output_table_name,
              output_table_name,
              res.colors1,
              res.colors2,
              i,
              this.show_table_name
            );
            break;
          case "separate_columns":
            res = generateDataForSeparateColumn(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              output_explict_col
            );
            separate_columns(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.inExp,
              res.outExp,
              i,
              this.show_table_name
            );
            break;
          case "separate_rows":
            res = generateDataForSeparateRows(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col
            );
            separate_rows(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              res.outColors,
              i,
              this.show_table_name
            );
            break;
          case "combine_tables_extend":
            res = generateDataForTablesExtend(
              dataIn1_csv,
              dataIn2_csv,
              dataOut1_csv
            );
            combine_tables_extend(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              input_table_name2,
              output_table_name,
              res.outColors,
              i,
              this.show_table_name
            );
            break;
          case "combine_tables_left_join":
            //需要确定空值的表示形式，暂时以''表示空值
            res = generateDataForLeftJoin_2(
              dataIn1_csv,
              dataIn2_csv,
              dataOut1_csv,
              input_explict_col,
              'NA'
            );
            combine_tables_left_join(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              input_table_name2,
              output_table_name,
              res.naCol,
              res.naPos,
              res.inColor1,
              res.inColor2,
              res.outColor,
              i,
              this.show_table_name
            );
            break;
          case "combine_tables_full_join":
            res = generateDataForFullJoin_2(
              dataIn1_csv,
              dataIn2_csv,
              dataOut1_csv,
              input_explict_col,
              'NA'
            );
            console.log(res)
            combine_tables_full_join(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              input_table_name2,
              output_table_name,
              res.naCol,
              res.naPos,
              res.inColor1,
              res.inColor2,
              res.outColor,
              i,
              this.show_table_name
            ),
              this.show_table_name;
            break;
          case "combine_tables_inner_join":
            res = generateDataForInnerJoin(
              dataIn1_csv,
              dataIn2_csv,
              dataOut1_csv,
              input_explict_col,
              ""
            );
            combine_tables_inner_join(
              res.m1,
              res.m2,
              res.m3,
              rule,
              input_table_name,
              input_table_name2,
              output_table_name,
              res.inColors2,
              res.outColor,
              i,
              this.show_table_name
            );
            break;
          case "transform_tables_fold":
            res = generateDataForFold(
              dataIn1_csv,
              dataOut1_csv,
              input_explict_col,
              output_explict_col
            );
            transform_tables_fold(
              res.m1,
              res.m2,
              rule,
              input_table_name,
              output_table_name,
              input_explict_col.length,
              i,
              this.show_table_name
            );
            break;
          case "transform_tables_unfold":
            output_explict_col = [];
            for (let col = 0; col < dataOut1_csv[0].length; col++) {
              if (dataIn1_csv[0].indexOf(dataOut1_csv[0][col]) === -1) {
                output_explict_col.push(col);
              }
            }
            res = generateDataForFold(
              dataOut1_csv,
              dataIn1_csv,
              output_explict_col,
              input_explict_col
            );
            let diffVals = new Set()
            for(let row = 1;row < dataIn1_csv.length;row++){
              diffVals.add(dataIn1_csv[row][input_explict_col[0]])
            }
            transform_tables_unfold(
              res.m2,
              res.m1,
              rule,
              input_table_name,
              output_table_name,
              diffVals.size,
              i,
              this.show_table_name
            );
            break;
        }
      }
    },
  },
  /* # 同时监听到两个值的变化再执行方法
  computed: {
    address() {
      const { script_content, language } = this;
      return { script_content, language };
    },
  },
  watch: {
    address(val) {
      console.log(val);
      this.changeModel(val.script_content, val.language)
    },
  },
  */
  mounted() {
    this.initData();
  },
};
</script>

